.header {
  padding: 24px 0;
  border-bottom: 1px solid #EFEFFF;

  @include tablet {
    position: relative;
  }

  @include mobile {
    padding: 20px 0;
  }

  &-container {
    @include flex-all-sb;
  }

  &__control {
    @include flex-all-sb;
  }

  &__logo {
    width: 120px;
    height: 40px;
    margin-right: 45px;
  }

  &__main {
    display: flex;
    align-items: center;
  }

  &__btn {
    padding: 12px 18px;

    @include tablet {
      margin-right: 20px;
    }
  }
}
