.blog-content {

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 6 span;

      &:last-child {
        grid-column: 12 span;

        @include mobile {
          grid-column: 4 span;
        }
      }
    }

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__inner {
    padding: 40px;
    border: 1px solid #EFEFFF;
    box-shadow: 0px 4px 20px #F7F6FB;
    border-radius: 20px;

    @include mobile {
      padding: 24px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
    line-height: 133%;
  }

  &__time {
    color: var(--blue-color);
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 500;
    color: var(--grey-color58);
  }

  &__control {
    @include flex-all-sb;
  }

  &__links {
    @include flex-v-center;
  }

  &__news {
    padding: 5px 20px;
    background: #EFEFFF;
    border-radius: 4px;
    font-weight: 500;
    line-height: 150%;
  }

  &__word {
    margin-right: 10px;
  }
}
