.footer {
  padding: 80px 0 100px 0;
  background-color: var(--accent-color);

  &__content {
    align-items: center;
  }

  &__info {
    grid-column: 4 span;

    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__logo {
    display: block;
    margin-bottom: 30px;
    width: 180px;
    height: 62px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 48px;
    font-weight: 500;
    font-size: 14px;
    max-width: 292px;
    opacity: 0.8;
    line-height: 120%;
    color: var(--light-color);
  }

  &__nav {
    grid-column: 8 span;

    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }
}

.footer-nav {

  &__col {
    grid-column: 4 span;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 4 span;
    }


  }

  // &__tel {

  // }
}
