.form {

  &__tel {
    display: inline-block;
    font-size: 14px;
    margin-bottom: 8px;
    font-weight: 500;
  }

  &__field {
    display: flex;
    margin-bottom: 25px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__input {
    padding: 10px;
    width: 100%;
    margin-right: 20px;
    border-radius: var(--border-8);

    @include mobile {
      height: 55px;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__submit {
    white-space: nowrap;
  }

  &__accord {
    display: flex;
    align-items: flex-start;
  }

  &__checkbox {
    margin-right: 15px;
    cursor: pointer;
  }

  &__agreement {
    font-weight: 500;
  }

  &__link-agreement {
    color: var(--accent-color);
  }
}
