.nav {
  // margin: 0 10px;

  @include tablet {
    display: none;
    // background-color: red;
  }

  &__list {
    @include flex-v-center;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 45px;

      @include tablet {
        margin: 0;
        margin-bottom: 40px;
      }
    }
    @include tablet {
      text-align: center;
    }
  }

  &__link {
    font-weight: 600;
    color: var(--grey-color);

    @include tablet {
      margin-bottom: 80px;
      color: var(--light-color);
      font-size: 36px;
      line-height: 120%;
      // text-align: center;
    }
  }



}
