.footer-nav {

  &__col {

    &--contacts {

      & .footer-nav__item {

        &:nth-child(2) {
          margin-bottom: 0;
        }

        &:last-child {
          margin-bottom: 40px;
        }
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 24px;
    line-height: 133%;
    color: var(--light-color);
  }

  // &__list {}

  &__item {

    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }

  &__link {
    font-weight: 500;
    color: var(--light-color);
    opacity: 0.8;
    line-height: 150%;
  }

  &__tel {
    font-weight: 700;
    font-size: 18px;
    opacity: 1;
  }

  &__mail {
    font-weight: 400;
  }

  &__address {
    font-style: normal;
    color: var(--light-color);
    opacity: 0.8;
  }
}
