.faq-accordion {

  &__item {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgba(46, 144, 209, 0.08);

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__inner {
    border-radius: 10px;
    transition: background-color 0.3s ease-in-out;
  }

  &__control {
    @include flex-all-sb;
    width: 100%;
    padding: 24px;
  }

  &__title {
    margin: 0;
    text-align: left;
  }

  &__span {
    margin-left: 15px;
    display: flex;
    border-radius: 4px;
    transition: background-color 0.3s ease-in-out
  }

  &__icon {
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;

    & path {
      transition: stroke 0.3s ease-in-out
    }
  }

  &__content {
    padding: 0;
    // padding: 24px;
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    transition: opacity 0.3s ease-in-out, will-change 0.3s ease-in-out, max-height 0.3s ease-in-out, overflow 0.3s ease-in-out, padding 0.3s ease-in-out;
    box-sizing: content-box;
  }

  &__text {
    margin: 0 24px 24px 24px
  }
}

.open {

  & .faq-accordion {

    &__inner {
      background-color: #EFEFFF;
    }

    &__content {
      opacity: 1;
    }

    &__icon {
      transform: rotate(-45deg);

      & path {
        stroke: var(--light-color);
      }
    }

    &__span {
      background-color: var(--accent-color);
    }
  }
}
