.reviews {
  margin-bottom: 100px;

  &__main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__title {
    margin: 0;
    max-width: 440px;

    @include tablet {
      margin-bottom: 24px;
    }
  }

  &__content {
    align-items: center;
  }

  &__slider {
    grid-column: 6 span;

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__img {
    grid-column: 6 span;

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__swiper {
    // background-color: #fdede4;
    border-radius: 10px;
  }
}
