.burger {
  @include burger;
  display: none;

  @include tablet {
    display: block;
  }
}

@include tablet {
  .nav {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    z-index: 5;
    display: block;
    transform: translateX(120%);
    overflow-y: auto;
    visibility: hidden;
    background-color: var(--accent-color);
    transition: visibility .3s ease-in-out, transform .3s ease-in-out;
    // transition: visibility .3s ease-in-out;
  }

  .menu--active {
    transform: none;
    visibility: visible;
    background-color: var(--accent-color);

    & .nav__list {
      opacity: 1;
      transition: opacity 1.2s ease-in-out;
    }
  }

  .nav__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    opacity: 0;
    transition: opacity 0.12s ease-in-out;
  }
}

