.swiper-reviews {
  // background-color: #fdede4;
  // height: 362px;

  &__slide {
    // height: 322px;
    background-color: #fdede4;
    border-radius: 10px;
  }

  &__main {
    padding: 65px 40px;

    @include mobile {
      padding: 65px 25px;
    }

  }

  &__control {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__student {
    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__naim {
    margin: 0;
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 20px;
    color: var(--title-color);
  }

  &__status {
    font-weight: 500;
    font-size: 16px;
    color: var(--grey-color82);


  }

  &__text {
    margin: 0;
    font-weight: 500;
    // font-size: 16px;
    color: #4F4F4F;
  }
}
