.hero-control {

  @include flex-v-center;

  @include tablet {
    justify-content: center;
  }

  @include small-tablet {
    flex-direction: column;
  }

  @include mobile {
    margin-bottom: 50px;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 32px;

      @include small-tablet {
        margin: 0;
      }

      @include mobile {}
    }

    &:first-child {
      @include small-tablet {
        margin-bottom: 24px;
      }
    }
  }

  &__video {
    @include flex-v-center;
  }

  &__play {
    margin-right: 12px;
  }

  &__text {
    color: var(--grey-color58);
    font-weight: 500;
  }
}
