.reg {
  margin-bottom: 100px;

  & .container {
    @include small-tablet {
      padding: 0;
    }
  }

  &__title {

    @include mobile {
      font-size: 30px;
    }
  }

  &__inner {
    align-items: center;
    padding: 70px 155px 70px 100px;
    background: #EFEFFF;
    border-radius: 40px;

    @include tablet {
      padding: 50px;
    }

    @include small-tablet {
      padding: 70px 40px;
      border-radius: 0;
    }

    @include mobile {
      padding: 50px 15px;
    }
  }

  &__content {
    grid-column: 6 span;

    @include tablet {
      grid-column: 8 span;
    }

    @include small-tablet {
      margin-bottom: 20px;
    }

    @include small-tablet {
      grid-column: 12 span;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  &__img {
    grid-column: 6 span;
    display: flex;
    justify-content: flex-end;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 12 span;
      justify-content: center;
    }

    & .image {
      max-height: 380px;
    }
  }
}
