.faq {
  margin-bottom: 150px;

  // &__inner {}

  &__main {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-column: 4 span;

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
  }

  &__btn {
    margin-bottom: 60px;
  }

  &__picture {
    display: flex;
    align-self: center;
  }

  // &__img {}

  &__accordion {
    grid-column: 8 span;

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }
}
